.block21 .items{position:relative;display:grid;grid-template-columns:45% 55%;gap:40px;padding:100px 0}.block21 .items:before{position:absolute;top:0;left:-48px;width:calc(100% + 96px);height:100%;border-radius:15px;background:var(--color-white-80);content:"";z-index:1}.block21 .items .item{position:relative;z-index:2}.block21 .items .item:first-child{padding:0}.block21 .items .item:first-child .text{padding:30px 0 0;font-weight:300}.block21 .items .item:first-child img{transform:rotate(30deg);transition:none}.block21 .items .item:last-child .swiper .swiper-slide{padding:10px 0 0}.block21 .items .item:last-child .swiper .swiper-slide ul li:last-child button{border:0}.block21 .items .item:last-child .swiper .swiper-slide ul li button{justify-content:space-between;gap:20px;width:100%;padding:34px 0;border-bottom:1px solid var(--color-solid-40)}.block21 .items .item:last-child .swiper .swiper-slide ul li button span{display:block;text-align:left}.block21 .items .item:last-child .swiper .swiper-slide ul li button svg{max-width:38px;min-width:38px;height:38px}.block21 .items .item:last-child .swiper .swiper-slide ul li button svg use{fill:var(--color-text-hover)}